let Pagination = (props)=>
{   let pages = [];
    for(let i = 1;i<=props.totalPages;i++)
    {
        pages.push(i);
    }
    
    
    return (
        <div class="row">
                <nav aria-label="Page navigation example">
                    <ul class="pagination">
                    {
                            pages.map((ele)=>
                            {
                                return (
                                <li 
                                class={`page-item ${ele==props.currPage?"active":""}`}
                                onClick = {()=>
                                {
                                    props.handlePageChange(ele);
                                }}
                                ><a class="page-link" href="#">{ele}</a></li>);
                            })
                    }
                    </ul>
                </nav>
        </div>
    )
}
export default Pagination;