let Search = (props)=>
{
    return (
        <>
        <div className = "row">
          <div class="col-3 mt-4">
                <div class="input-group input-group-sm mb-3">
                   <input type="text" placeholder="search movie" class="form-control" aria-label="Sizing example input" aria-describedby="inputGroup-sizing-sm"
                   
                    onChange = {(e)=>
                    {
                        props.searchedChanged(e.currentTarget.value);
                    }}
                   ></input>
                </div>
          </div>
        </div>
        <div className = "row row-no-gutters">
            <div class = "col-2">
                  <button type="button" class="btn btn-primary">New</button>
            </div>
        </div>
        </>
    );
}
export default Search;