let Filter = (props)=>
{
    return (
        <div class="col-3 m-4">
            <ul class="list-group ">
                <li onClick = {()=>props.handleFilterChange("All Genre")}class={`list-group-item ${props.selectedFilter == "All Genre"?"active":""}`}>All Genre</li>
                {
                    props.Genre.map((ele)=>{
                        return (
                        <li 
                        key = {ele._id} 
                        className = {`list-group-item  ${ele.name == props.selectedFilter?"active":""}`}
                        onClick = {()=>props.handleFilterChange(ele.name)}
                        >{ele.name}</li>
                        );
                    })
                
                }
                
              </ul>
              </div>
    );

}
export default Filter;