import Pagination from "./Pagination";
import React from "react";
import Search from "./Search";
class Table extends React.Component {
    state = {
        currPage:1,
        searched : ""
    }
   componentDidUpdate()
   {
       console.log('table updated');
   }

    handlePageChange = (pageno)=>
    {
            this.setState({currPage:pageno});
    }
    searchedChanged=(search)=>
    {
            this.setState({searched:search});
    }
   

    render = ()=>
    {
        let selectedFilter = this.props.selectedFilter;
        let filteredMovies = this.props.moviesData.filter((ele)=>{

        if(selectedFilter === "All Genre")
            return ele;
        else if(ele.genre.name === selectedFilter){
            
            return ele;
            
        }
    })
    filteredMovies = filteredMovies.filter((ele)=>
    {   let movieTitle = ele.title.toLowerCase();
        let searchedMovie = this.state.searched.toLowerCase();
        return movieTitle.includes(searchedMovie);
    })
    let totalPages =Math.ceil(filteredMovies.length/4);
    filteredMovies = filteredMovies.slice((this.state.currPage-1)*4,this.state.currPage*4);
    return (
        <>
            <Search
                searched = {this.searched}
                searchedChanged = {this.searchedChanged}
            />
            <div class="row">
                <div class="col-8">
                    <table class="table table-hover mt-4">
                        <thead>
                          <tr>
                            <th scope="col">Title</th>
                            <th scope="col">Genre</th>
                            <th scope="col">Stock</th>
                            <th scope="col">Rate</th>
                          </tr>
                        </thead>
                        <tbody>
                          {
                              filteredMovies.map((ele)=>{
                                    return (
                                        <tr>
                                            <td>{ele.title}</td>
                                            <td>{ele.genre.name}</td>
                                            <td>{ele.numberInStock}</td>
                                            <td>{ele.dailyRentalRate}</td>
                                            <td
                                            onClick = {()=>
                                                {
                                                    this.props.handleLike(ele._id);
                                                }
                                            }>
                                            {   (ele.like)?
                                                (<span class="material-icons-outlined">
                                                    favorite
                                                </span>)
                                                :
                                                (<span class="material-icons-outlined">
                                                    favorite_border
                                                </span>)
                                            }
                                            </td>
                                            <td>
                                                <button 
                                                className = "btn btn-danger"
                                                onClick = {()=>{
                                                    this.props.handleDelete(ele._id);
                                                }}
                                                >delete</button></td>
                                        </tr>


                                    );


                                    })

                          }
                        </tbody>
                      </table>
                </div>
            </div>
            <Pagination
                handlePageChange = {this.handlePageChange}
                currPage ={this.state.currPage}
                totalPages = {totalPages}
            />
        </>
    );
}
    


}

export default Table;